body {
	font-size: 16px;
	color: rgb(12, 12, 12);
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, select, option,
div, input {
	margin: 0;
	padding: 0;
}

.mainPage {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;

	.mainPageWrapper {
		padding: 15px;
		margin: 0 auto;
	}
	.dataControlWrap {
		margin: 40px auto;
		width: 50%;
		display: flex;
		align-items: center;
		gap: 10px;
		color: #999;
		font-size: 1em;
		.changeBtn {
			font-size: 1.2em;
			outline: none;
			border: none;
			background-color: #71C838;
			color: #fff;
			padding: 10px 15px;
			cursor: pointer;
			transition: .1s linear;
			
			&:hover, &:focus {
				background-color: #4ba80d;
			}
		}
	}
}

